import React, { useState, useEffect, useRef } from 'react'
import Swiper from 'swiper'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { useHistory } from 'react-router'
import { useSelector } from 'react-redux'
import { useWebpImg, filterBannerItem } from '@/utils/util'
import scss from './banner.module.scss'
import { playGame } from '@/utils/gameUtil'

const cx = classNames.bind(scss)

function areEqual(prevProps, nextProps) {
  return prevProps.field === nextProps.field
}

function Banner(props) {
  const history = useHistory()
  const { field, swiperParams } = props
  const bannerEl = useRef(null)
  const banner = useSelector(state => state.banner)
  const [bannerList, setBannerList] = useState([])
  const defaultImg = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'

  useEffect(getBannerList, [field, banner])

  useEffect(initSwiper, [bannerList])

  function getBannerList() {
    const _useWebpImg = useWebpImg
    const wms = window.OMConfig.wmsServer + '/'
    if (!banner[field]) return
    let bannerList = banner[field].slider_data
    bannerList = bannerList.filter(item => {
      item.imgUrl = `${wms}${item.imgurl}${_useWebpImg('_')}`
      return filterBannerItem(item)
    })
    setBannerList(bannerList)
  }

  function initSwiper() {
    let _swiperParams = {
      loop: bannerList.length !== 1,
      lazy: {
        loadPrevNext: true
      },
      autoplay: {
        delay: 5000,
        disableOnInteraction: false
      },
      pagination: {
        el: '.swiper-pagination',
        clickable: true
      }
    }
    _swiperParams = {
      ..._swiperParams,
      ...swiperParams
    }
    const swiper = bannerList.length && new Swiper(bannerEl.current, _swiperParams)
    return () => swiper && swiper.destroy(true)
  }

  function handelClick(item) {
    if (item.action.detail) {
      switch (item.action.type) {
        case '1':
          return openLing(item.action.detail, item.target)
        case '2':
          return loadGame(item.action.detail)
        case '3':
          // 播放视屏
          break
      }
    }

    function openLing(link, target) {
      link = link.replace(/^\//, '')
      if (+target === 1) {
        window.open(link)
      } else {
        history.push(link)
      }
    }

    function loadGame(code) {
      code = code.split('&')
      playGame({
        gameCode: code[0],
        gameId: code[1],
        gameType: code[2],
        gameName: code[3],
        canTryPlay: !!code[4]
      })
    }
  }

  return (
    <div ref={bannerEl} className={cx('banner', 'default-bg', 'swiper-container')}>
      <div className={cx('swiper-wrapper')}>
        {bannerList.map((item, index) => {
          return (
            <div key={index.toString()}
              onClick={() => handelClick(item, index)}
              className={cx('banner__item', 'default-bg', 'swiper-slide', { cursor: item.action.detail })}>
              <img data-src={item.imgUrl} className={cx('banner__img', 'swiper-lazy')} src={defaultImg}
                alt={item.title}/>
            </div>
          )
        })}
      </div>
      <div className={cx('swiper-pagination')}/>
    </div>
  )
}

Banner.defaultProps = {
  field: 'home'
}

Banner.propTypes = {
  swiperParams: PropTypes.object,
  field: PropTypes.string.isRequired
}

export default React.memo(Banner, areEqual)
