import { createStore, applyMiddleware } from 'redux'
import thunkMiddleware from 'redux-thunk'
import state from '@/store/state.js'
import reducer from './reducer.js'

const store = createStore(
  reducer,
  state,
  applyMiddleware(thunkMiddleware)
)

store.subscribe(() => mapState(store))

function mapState(store) {
  const cacheState = {}
  const mapStateKey = [
    'token',
    'banner',
    'customer',
    'balances',
    'gameStatus'
  ]
  const state = store.getState()
  const cacheTime = Date.now() + 30 * 60 * 1000
  mapStateKey.forEach(key => {
    cacheState[key] = state[key]
  })
  cacheState._cacheTime = cacheTime
  window.localStorage.setItem('redux_state', JSON.stringify(cacheState))
}

export default store
