import http from '@/api/http.js'
import { baseGateway, productGateway } from '@/api/api_config.js'

let result = null

// 获取token
export function getWebToken(params, headers) {
  params = {
    ...params,
    _isNeedToken: false,
    _isNeedLoading: false
  }
  const url = `${baseGateway}/webToken`
  result = result || new Promise(resolve => {
    http.post(url, params, headers).then(({ head, body }) => {
      if (head.errCode === '0000') {
        resolve(body.info)
      }
      resolve(null)
      result = null
    }).catch(() => {
      resolve(null)
      result = null
    })
  })
  return result
}

// 获取游戏状态
export function getGameStatus(params, headers) {
  return http.post(`${baseGateway}/game/queryGame`, params, headers)
}

// 获取游戏状态
export function inGame(params, headers) {
  return http.post(`${baseGateway}/game/inGame`, params, headers)
}

// 获取余额
export function getBalances(params, headers) {
  return http.post(`${baseGateway}/getBalances`, params, headers)
}

// 获取服务器时间
export function getTime(params, headers) {
  return http.post(`${productGateway}/get-time`, params, headers)
}

// 获取图片验证码
export function getCaptcha(params, headers) {
  return http.post(`${baseGateway}/captcha/generate`, params, headers)
}

// 获取图片验证码
export function getIsNeedCaptcha(params, headers) {
  return http.post(`${baseGateway}/customer/preLogin`, params, headers)
}

// 获取图片验证码
export function sendCode(params, headers) {
  return http.post(`${baseGateway}/phone/sendCode`, params, headers)
}

export default {
  getWebToken,
  getGameStatus,
  getBalances,
  getTime,
  getCaptcha,
  getIsNeedCaptcha,
  sendCode,
  inGame
}
