'use strict'

import qs from 'qs'
import md5 from 'js-md5'
import axios from 'axios'
import config from '@/config/config.js'
import { history } from '@/router/index.js'
import store from '@/store/index.js'
import { dialog } from '@/components/dialog/Dialog.js'
import { loading } from '@/components/loading/Loading.js'
import { getWebToken } from '@/store/actions/common.js'
import { setSignOut } from '@/store/actions/sign.js'
import { getUUId, sixRandom, reverseString } from '@/utils/util.js'
import { toggleLoginDialog } from '@/store/actions/common'

// 请求中的id列表
let requestIdList = []

// 创建 axios 实例
const _http = axios.create({
  timeout: 30 * 1000,
  baseURL: config.apiUrl,
  headers: {
    v: config.version,
    AppId: config.appId,
    domainName: config.apiUrl
  }
})

function setHeader(_config, isNeedToken, state) {
  return _getWebToken(isNeedToken).then(token => {
    token = (token && JSON.parse(window.atob(token)))
    const qid = md5(Date.now() + sixRandom())
    const dataStr = reverseString(JSON.stringify(_config.data))
    const tokenStr = token ? (token.token + token.u2token) : ''
    const sign = dataStr + qid + config.appId + config.version + tokenStr
    const header = {
      Sign: md5(sign),
      Qid: qid,
      token: token ? token.token : null
    }
    _config.headers = {
      ..._config.headers,
      ...header
    }
    return _config
  })

  function _getWebToken(isNeedToken) {
    if (isNeedToken) {
      return store.dispatch(getWebToken())
    } else {
      return Promise.resolve(state.token)
    }
  }
}

// 添加请求拦截器
_http.interceptors.request.use(config => {
  let data = {
    productId: 'A02',
    ...config.data
  }
  const state = store.getState()
  const isNeedToken = data._isNeedToken !== false
  const isNeedLoading = data._isNeedLoading !== false
  const isNeedLoginName = data._isNeedLoginName !== false
  const isShowErrorTips = data._isShowErrorTips !== false
  const notShowErrorTipsCode = data._notShowErrorTipsCode

  delete data._isNeedToken
  delete data._isNeedLoading
  delete data._isNeedLoginName
  delete data._isShowErrorTips
  delete data._notShowErrorTipsCode

  if (isNeedLoading !== false) {
    const uuid = getUUId()
    loading.open()
    config._requestId = uuid
    requestIdList.push(uuid)
  }
  if (isNeedLoginName && !data.loginName) {
    data.loginName = state.customer.loginName
  }
  if (new RegExp('application/x-www-form-urlencoded').test(config.headers['Content-Type'])) {
    data = qs.stringify(data)
  }

  config.data = data
  config._isShowErrorTips = isShowErrorTips
  config._notShowErrorTipsCode = notShowErrorTipsCode
  return setHeader(config, isNeedToken, state)
}, error => {
  loading.hide()
  console.log('request error:\n', error)
  return Promise.reject(error)
})

// 添加响应拦截器
_http.interceptors.response.use(response => {
  const { head } = response.data
  const config = response.config
  const code = head && head.errCode
  const message = head && head.errMsg
  const notTipsCode = ['0000']
  const needLoginCodes = ['GW_802154', 'GW_890206', 'GW_890209']
  const dialogId = window.btoa('_http.interceptors.response')
  const isTips = code && config._isShowErrorTips && code !== config._notShowErrorTipsCode && !~notTipsCode.indexOf(code)
  requestIdList = requestIdList.filter(item => item !== config._requestId)
  if (!requestIdList.length) loading.hide()
  if (code && ~needLoginCodes.indexOf(code)) {
    dialog({
      message,
      callback() {
        store.dispatch(toggleLoginDialog(true))
      }
    }, dialogId)
    store.dispatch(setSignOut())
    history.push('/home', { form: history.location.pathname })
  } else if (isTips) {
    dialog(message, dialogId)
  }
  response.data.success = code === '0000'
  return response.data
}, (error) => {
  loading.hide()
  console.log('response error:\n', error)
  return Promise.reject(error)
})

// 封装常用请求方法
const http = {
  get(url, params = {}, headers = {}) {
    const options = {}
    options.params = params
    options.headers = headers
    return _http.get(url, options)
  },
  post(url, params, headers = {}) {
    return _http.post(url, params, headers)
  },
  put(url, params, headers = {}) {
    return _http.put(url, params, { headers })
  },
  delete(url, params = {}, headers = {}) {
    const options = {}
    options.params = params
    options.headers = headers
    return _http.delete(url, options)
  }
}

export default http
